.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.title {
  animation: titleBounce infinite 5s linear;
  animation-direction: alternate;

}

@keyframes titleBounce {
  from {
    transform: scale(1.2);
  }
  to {
    transform: scale(1);
  }
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.super_container_1 {
  background-color: rgba(0.0, 0.0, 0.0, 0.2);
    width: 100%;
}

.container {
  margin:auto;
  max-width: 1000px;
  padding-left: 1em;
  padding-right: 1em;
  padding-top: 3em;
  padding-bottom: 3em;

}


@media ( min-aspect-ratio: 1.2/1 ) {
  .title-grid {
    display: grid;
    grid-template-columns: 50% 50%;
    grid-gap: 10px;
    grid-template-areas: "title cellphone";
  }

  .mount {
    top: -30vh;
  }

  .respSection {
    flex-direction: row;
  }

  .respSectionRight {
    flex-direction: row-reverse !important;
  }

  .respSection > div:first-child {
    flex-grow: 4;
    flex-basis: 30%;
  }

  .respSection > div:nth-child(2n) {
    flex-grow: 4;
    flex-basis: 60%;
  }

  .logos {
    flex-direction: row;
  }

  .logos > img {
    margin: 1em;
    width: 30%;
  }
}

.logos {
  display:flex;
  align-items:center;
  justify-content:center;

}

.logos > img {
  margin: 1em;
}


.respSection {
  display:flex;
  align-items: center;
  justify-content: center;
}

@media ( max-aspect-ratio: 1.2/1 ) {
  .title-grid {
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 70vw 100%;
    grid-gap: 10px;
    grid-template-areas: "title" "cellphone" ;

  }

  .mount {
    top: 0vh;
  }

  .respSection {
    flex-direction: column;
    font-size: 3vw;
  }

  .respSection > div:first-child {
    width: 100%;
  }

  .logos {
    flex-direction: column;

  }

  .logos > img {
    margin: 1em;
    width: 50%;
  }
}

.animal {
  animation: animal 2s ease-in-out infinite;
  transform-origin: center center;
  animation-direction: alternate;
  image-rendering: pixelated;
}

.species-box {
  display: flex;
  flex-direction: column;
  box-sizing:border-box;
  align-items: center;
  margin: 0.3em;
  width: 5em;
  height: 5em;
  padding: 0.5em;
  border-width: 3px;
  border-color: white;
  border-radius: 1em;
  border-style: solid;
}

.species-img {
  display: block;
  width: 70%;
  height: 70%;
  image-rendering: pixelated;
  object-fit: contain;
}

.species-img-2 {
  display: block;
  max-width: 100%;
  max-height:100%;
  height: auto;
  image-rendering: pixelated;
  object-fit: contain;
}

.lander {
  animation: lander 2s ease-in-out infinite;
  transform-origin: center center;
  animation-direction: alternate;
  image-rendering: pixelated;
}

.mount {
  animation: mount 20s ease-in-out infinite;
  transform-origin: top center;
}

.mount1 {
  animation-delay: -5s;
}

.mount2 {
  animation-delay: -10s;
}

.mount3 {
  animation-delay: -15s;
}

.mount4 {
  animation-delay: 0s;
}


@keyframes mount {
  0% {
    transform: scale(3);
    filter: brightness(1);
    -webkit-filter: brightness(1);
    z-index: -1000;
    opacity: 0%;

  }
  10% {
    opacity: 100%;
  }

  90% {
    opacity: 100%;
  }
  100% {
    transform: scale(1);
    filter: brightness(2);
    -webkit-filter: brightness(1.5);
    z-index: -2000;
    opacity: 0%;
  }
}

.credits > h4 {
  margin-top: 2em;
  margin-bottom: 0.5em;
}

@media ( max-aspect-ratio: 1.2/1 ) {
  .credits {
    font-size: 0.7em;
  }
}

.credits > p {
  margin-top: 0em;
  margin-bottom: 0em;
}

.link-button {
  background-image: url(../public/button_bg.png);
  padding: 1.7em;
  margin: 1em;
  background-size: 100% 100%;
  width: 50%;
  image-rendering: pixelated;

}

.link-button-dialog {
  background-image: url(../public/button_bg.png);
  display:inline-block;
  padding: 1.3em 1.5em;
  margin-top: 1em;
  margin-left: 0.5em;
  margin-right:0.5em;
  background-size: 100% 100%;

  image-rendering: pixelated;
  font-size: 0.7em;

}

a {
    text-decoration: none;
    color: white;
}


@keyframes animal {
  0% {
    transform: scale(1.1, 0.9);

  }
  100% {
    transform: scale(0.9, 1.1);
  }
}

@keyframes lander {
  0% {
    transform: translateY(11px);

  }
  100% {
    transform: translateY(-11px);
  }
}

.cd {
  animation: cd 1s ease-in-out infinite;
  transform-origin: center center;
  animation-direction: alternate;
}

@keyframes cd {
  0% {
    transform: rotate(5deg);

  }
  100% {
    transform: rotate(-5deg);
  }
}



